<template>
  <v-dialog v-model="visible" persistent max-width="600px">
    <v-card>
      <v-card-title>
        <span class="headline"></span>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-col cols="12">
            <v-text-field
              v-model.number="internalValue.timeCorrection"
              label="  ()"
              type="number"
            />
          </v-col>
          <v-col cols="12">
            <v-text-field
              v-model.number="internalValue.travelTimeCorrection"
              label="   ()"
              type="number"
            />
          </v-col>
          <v-col cols="12">
            <v-select
              v-model="internalValue.travelMode"
              :items="TRAVEL_MODES_VALUES"
              return-object
              label="  "
              outlined
            />
          </v-col>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn color="blue darken-1" text @click="closeModal()">
          
        </v-btn>
        <v-btn color="blue darken-1" text @click="updateValue()">
          
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
  import { TRAVEL_MODES_VALUES } from '@/constants/travel-modes'

  export default {
    name: 'EditItineraryItemModal',
    props: {
      item: {
        type: Object,
        required: true,
      },
    },
    data () {
      return {
        visible: true,
        internalValue: { ...this.item },
      }
    },
    created () {
      this.TRAVEL_MODES_VALUES = TRAVEL_MODES_VALUES
    },
    methods: {
      updateValue: function () {
        this.$emit('update', this.internalValue)
        this.$emit('close')
      },
      closeModal: function () {
        this.$emit('close')
      },
    },
  }
</script>
